/*Elementos generales*/

* {
    margin:0;
    padding:0;
    box-sizing: border-box;
}

html {
    font-family: 'Share Tech Mono', sans-serif;
    color:black;
    background-color: black;
}

body{

    padding:0;
    font-size:14px;
    overflow: hidden;
}
.container-cover {
    background-image: url(../img/portadabackground.jpg);
    background-size:cover;
    background-repeat: no-repeat;
    min-height: 99vh;
    width: 99vw;
    overflow: hidden;
}

.content-cover{

    background-color: rgba(0, 0, 0, 0.5);
    width: 40%;
    text-align: center;
    top: 10%;
    min-height: 80%;
    min-width: 40%;
    position: absolute;
    right: 0;
    margin-right: 10vw;
    overflow-y: hidden;
    overflow-x: hidden;


}

.nav-scroll-cover ul{
    list-style: none;
    background-position:right;
    padding-top: 2%;
    padding-bottom: 2%;
    background-color: rgba(30,51,32,255);

}


.nav-scroll-cover ul li a{
    text-decoration: none;
    color:white;
    font-size: 1rem;
    line-height: 2vh;
    padding: 2.5%;
}

.nav-scroll-cover ul li{
    display: inline;
}

.nav-scroll-cover ul a:hover{
    color:black;

}

.nav-scroll-cover p{
    font-size: 1.2rem;
    padding: 5%;
}

.text-cover{
    overflow-y: scroll;
    overflow-x: hidden;
    height:75vh;
    color:white;
    padding-left: 5%;
    padding-right: 5%;
    text-align: justify;
    line-height: 2rem;
}
.header h1{
    text-align: center;
    font-size: 3rem;
    padding: 5% 5% 2.5% 5%;
}
.header h2{
    text-align: center;
    font-size: 1.5rem;
    padding: 2.5% 5% 5% 5%;
}

header h1 a{
    text-decoration: none;
    color: white;
    letter-spacing: 1vw;
}

.footer-cover{
    position: fixed;
    bottom: 0px;
    right: 13%;
    color: white;
}

/*Categoria*/

.container-gen{
    padding-left: 10vw;
    padding-right: 10vw;
    background-color: rgba(30,51,32,255);
}

.header-gen{
    padding-top: 5vh;
    padding-bottom: 5vh;
}
.header-gen *{
    font-size: 0.8rem;
    text-align: left;
    color:white;
}


.nav-scroll-gen ul li{
    display:inline-block;
    text-align: left;
    padding-right: 40px;
    text-decoration: none;
    margin-bottom: 1vh;
}

.nav-scroll-gen ul li a{
    text-decoration: none;
    color: white;
}

.category-wrapper{
    
    background-color:rgba(0,0,0,0.5);
    height: 80vh;
    border:1px solid;
    border-color: white;
    display: flex;
}

.category-wrapper .categorias ul{
    text-align: left;
    list-style-type: none;
    width: 10vw;
    min-height: 80vh;
    display:grid;
}

.categorias ul button{
    width:100%;
    height: 100%;
    border-radius: 25%;
}

.text-category {
    color:white;
    text-align: justify;
    padding-left: 5%;
    padding-right: 5%;
    padding-top: 20%;
    overflow-y:scroll;
}